"use client";

import React, { useState, useEffect } from 'react';
import { cheeseMenu, seafoodMenu, drinksMenu, MenuItem } from '@/lib/menuData';

export interface MenuSelection {
  [itemId: string]: number;
}

export interface SpicySelection {
  [itemId: string]: boolean;
}

interface OrderFormProps {
  selections: MenuSelection;
  spicySelections: SpicySelection;
  calcMenuTotal: (selections: MenuSelection) => number;
  calcSpicyTotal: (selections: MenuSelection, spicySelections: SpicySelection) => number;
}

const OrderForm: React.FC<OrderFormProps> = ({ selections, spicySelections, calcMenuTotal, calcSpicyTotal }) => {
  // Form fields state
  const [name, setName] = useState("");
  const [address, setAddress] = useState("");
  const [phone, setPhone] = useState("");
  const [orderType, setOrderType] = useState<"delivery" | "pickup">("pickup");
  const [messagingOption, setMessagingOption] = useState<"whatsapp" | "sms" | "viber">("whatsapp");

  // Error state
  const [errors, setErrors] = useState<{ [key: string]: string }>({});

  // Total amounts
  const [menuTotal, setMenuTotal] = useState(0);
  const [spicyTotal, setSpicyTotal] = useState(0);
  const [grandTotal, setGrandTotal] = useState(0);

  // Check if there are any items selected
  const hasItemsSelected = Object.values(selections).some(qty => qty > 0);

  // Recalculate totals when selections or options change
  useEffect(() => {
    const currentMenuTotal = calcMenuTotal(selections);
    const currentSpicyTotal = calcSpicyTotal(selections, spicySelections);
    
    setMenuTotal(currentMenuTotal);
    setSpicyTotal(currentSpicyTotal);
    setGrandTotal(currentMenuTotal + currentSpicyTotal);
  }, [selections, spicySelections, calcMenuTotal, calcSpicyTotal]);

  const validateForm = () => {
    let currentErrors: { [key: string]: string } = {};
    
    if (!name.trim()) currentErrors.name = "Name is required.";
    if (!address.trim()) currentErrors.address = "Address is required.";
    if (!phone.trim()) currentErrors.phone = "Phone Number is required.";
    if (!hasItemsSelected) currentErrors.items = "Please select at least one item.";
    
    setErrors(currentErrors);
    return Object.keys(currentErrors).length === 0;
  };

  const buildOrderMessage = () => {
    // Build order details message
    let message = `*üç° Nippon - nom Order üç°*\n\n`;
    message += `*üìã Customer Details:*\n`;
    message += `üë§ Name: ${name}\n`;
    message += `üìç Address: ${address}\n`;
    message += `üì± Phone: ${phone}\n`;
    message += `üöö Order Type: ${orderType === "delivery" ? "Delivery" : "Pick-up"}\n`;
    
    if (orderType === "delivery") {
      message += `üí∞ Delivery Fee: ‚Ç±20 base + ‚Ç±10 per km\n`;
    }
    
    message += `\n*üõí Detailed Order Breakdown:*\n`;
    
    // Loop through selections; only include items with quantity > 0
    const allItems: MenuItem[] = [...cheeseMenu, ...seafoodMenu, ...drinksMenu];
    allItems.forEach((item) => {
      const qty = selections[item.id] || 0;
      if (qty > 0) {
        const isSpicy = spicySelections[item.id] || false;
        const baseSubtotal = item.price * qty;
        const spicySubtotal = isSpicy ? qty * 2 : 0;
        const totalSubtotal = baseSubtotal + spicySubtotal;
        
        message += `\nüì¶ *${item.name}${isSpicy ? ' (Spicy)' : ''}*\n`;
        message += `   Quantity: ${qty}\n`;
        message += `   Unit Price: ‚Ç±${item.price}\n`;
        message += `   Base Amount: ‚Ç±${item.price} √ó ${qty} = ‚Ç±${baseSubtotal}\n`;
        if (isSpicy) {
          message += `   Spicy Extra: ‚Ç±2 √ó ${qty} = ‚Ç±${spicySubtotal}\n`;
        }
        message += `   Item Total: ‚Ç±${totalSubtotal}\n`;
      }
    });
    
    message += `\n*üíµ Order Summary:*\n`;
    message += `üç° Items Subtotal: ‚Ç±${menuTotal}\n`;
    
    if (spicyTotal > 0) {
      message += `üå∂Ô∏è Total Spicy Extra: ‚Ç±${spicyTotal}\n`;
    }
    
    if (orderType === "delivery") {
      message += `üöö Delivery Fee: To be calculated (‚Ç±20 base + ‚Ç±10 per km)\n`;
    }
    
    message += `\n*üéØ GRAND TOTAL: ‚Ç±${grandTotal}*`;
    if (orderType === "delivery") {
      message += ` + delivery fee`;
    }
    message += `\n\n`;
    message += `Thank you for choosing Nippon - nom! üôè\n`;
    message += `We will contact you shortly to confirm your order.`;

    return message;
  };

  const handlePlaceOrder = (e: React.FormEvent) => {
    e.preventDefault();
    if (!validateForm()) return;

    const message = buildOrderMessage();

    // Handle different messaging options
    switch (messagingOption) {
      case 'whatsapp':
        const encodedMessage = encodeURIComponent(message);
        const whatsappURL = `https://wa.me/639217521870?text=${encodedMessage}`;
        window.open(whatsappURL, "_blank");
        break;
        
      case 'sms':
        const smsMessage = encodeURIComponent(message);
        const smsURL = `sms:09217521870?body=${smsMessage}`;
        window.location.href = smsURL;
        break;
        
      case 'viber':
        const viberMessage = encodeURIComponent(message);
        const viberURL = `viber://chat?number=639217521870&text=${viberMessage}`;
        window.open(viberURL, "_blank");
        break;
    }
  };

  return (
    <div className="bg-white rounded-lg shadow-lg p-6 border border-pink-200">
      <h2 className="text-3xl font-bold text-red-500 mb-6 text-center">
        üõí Place Your Order
      </h2>
      
      {errors.items && (
        <div className="mb-4 p-3 bg-pink-100 border border-pink-300 rounded-md">
          <p className="text-pink-700 font-medium">{errors.items}</p>
        </div>
      )}

      <form onSubmit={handlePlaceOrder} className="space-y-6">
        {/* Customer Information */}
        <div className="space-y-4">
          <h3 className="text-xl font-semibold text-gray-800 border-b border-gray-200 pb-2">
            üë§ Customer Information
          </h3>
          
          <div>
            <label htmlFor="name" className="block text-gray-700 font-medium mb-1">
              Full Name *
            </label>
            <input
              id="name"
              type="text"
              value={name}
              onChange={(e) => setName(e.target.value)}
              className="w-full border rounded-md p-3 border-gray-300 focus:border-pink-400 focus:outline-none focus:ring-2 focus:ring-pink-200"
              placeholder="Enter your full name"
            />
            {errors.name && <p className="text-pink-600 text-sm mt-1">{errors.name}</p>}
          </div>

          <div>
            <label htmlFor="address" className="block text-gray-700 font-medium mb-1">
              Complete Address *
            </label>
            <textarea
              id="address"
              value={address}
              onChange={(e) => setAddress(e.target.value)}
              className="w-full border rounded-md p-3 border-gray-300 focus:border-pink-400 focus:outline-none focus:ring-2 focus:ring-pink-200"
              placeholder="Enter your complete address"
              rows={3}
            />
            {errors.address && <p className="text-pink-600 text-sm mt-1">{errors.address}</p>}
          </div>

          <div>
            <label htmlFor="phone" className="block text-gray-700 font-medium mb-1">
              Phone Number *
            </label>
            <input
              id="phone"
              type="tel"
              value={phone}
              onChange={(e) => setPhone(e.target.value)}
              className="w-full border rounded-md p-3 border-gray-300 focus:border-pink-400 focus:outline-none focus:ring-2 focus:ring-pink-200"
              placeholder="09XXXXXXXXX"
            />
            {errors.phone && <p className="text-pink-600 text-sm mt-1">{errors.phone}</p>}
          </div>
        </div>

        {/* Order Type */}
        <div className="space-y-4">
          <h3 className="text-xl font-semibold text-gray-800 border-b border-gray-200 pb-2">
            üöö Order Type
          </h3>
          
          <div className="grid grid-cols-2 gap-4">
            <label className={`p-4 border-2 rounded-lg cursor-pointer transition-all ${
              orderType === "pickup" 
                ? "border-red-500 bg-red-50" 
                : "border-gray-300 hover:border-gray-400"
            }`}>
              <input
                type="radio"
                name="orderType"
                value="pickup"
                checked={orderType === "pickup"}
                onChange={() => setOrderType("pickup")}
                className="sr-only"
              />
              <div className="text-center">
                <div className="text-2xl mb-2">üè™</div>
                <div className="font-semibold">Pick-up</div>
                <div className="text-sm text-gray-600">No delivery fee</div>
              </div>
            </label>

            <label className={`p-4 border-2 rounded-lg cursor-pointer transition-all ${
              orderType === "delivery" 
                ? "border-red-500 bg-red-50" 
                : "border-gray-300 hover:border-gray-400"
            }`}>
              <input
                type="radio"
                name="orderType"
                value="delivery"
                checked={orderType === "delivery"}
                onChange={() => setOrderType("delivery")}
                className="sr-only"
              />
              <div className="text-center">
                <div className="text-2xl mb-2">üõµ</div>
                <div className="font-semibold">Delivery</div>
                <div className="text-sm text-gray-600">‚Ç±20 + ‚Ç±10/km</div>
              </div>
            </label>
          </div>
          
          {orderType === "delivery" && (
            <div className="mt-4 p-4 bg-yellow-50 border border-yellow-200 rounded-lg">
              <p className="text-sm text-yellow-800">
                üìç <strong>Delivery Information:</strong> Our delivery fee is ‚Ç±20 base rate plus ‚Ç±10 per kilometer from our store. 
                The exact delivery fee will be calculated and confirmed when we contact you.
              </p>
            </div>
          )}
        </div>

        {/* Messaging Options */}
        <div className="space-y-4">
          <h3 className="text-xl font-semibold text-gray-800 border-b border-gray-200 pb-2">
            üì± Send Order Via
          </h3>
          
          <div className="grid grid-cols-3 gap-3">
            <label className={`p-3 border-2 rounded-lg cursor-pointer transition-all ${
              messagingOption === "whatsapp" 
                ? "border-green-500 bg-green-50" 
                : "border-gray-300 hover:border-gray-400"
            }`}>
              <input
                type="radio"
                name="messagingOption"
                value="whatsapp"
                checked={messagingOption === "whatsapp"}
                onChange={() => setMessagingOption("whatsapp")}
                className="sr-only"
              />
              <div className="text-center">
                <div className="text-xl mb-1">üí¨</div>
                <div className="font-semibold text-sm">WhatsApp</div>
              </div>
            </label>

            <label className={`p-3 border-2 rounded-lg cursor-pointer transition-all ${
              messagingOption === "sms" 
                ? "border-blue-500 bg-blue-50" 
                : "border-gray-300 hover:border-gray-400"
            }`}>
              <input
                type="radio"
                name="messagingOption"
                value="sms"
                checked={messagingOption === "sms"}
                onChange={() => setMessagingOption("sms")}
                className="sr-only"
              />
              <div className="text-center">
                <div className="text-xl mb-1">üì±</div>
                <div className="font-semibold text-sm">SMS</div>
              </div>
            </label>

            <label className={`p-3 border-2 rounded-lg cursor-pointer transition-all ${
              messagingOption === "viber" 
                ? "border-purple-500 bg-purple-50" 
                : "border-gray-300 hover:border-gray-400"
            }`}>
              <input
                type="radio"
                name="messagingOption"
                value="viber"
                checked={messagingOption === "viber"}
                onChange={() => setMessagingOption("viber")}
                className="sr-only"
              />
              <div className="text-center">
                <div className="text-xl mb-1">üìû</div>
                <div className="font-semibold text-sm">Viber</div>
              </div>
            </label>
          </div>
        </div>

        {/* Order Summary */}
        {hasItemsSelected && (
          <div className="bg-gray-50 rounded-lg p-4 space-y-2">
            <h3 className="text-xl font-semibold text-gray-800 mb-3">
              üí∞ Order Summary
            </h3>
            
            <div className="flex justify-between text-gray-700">
              <span>Items Total:</span>
              <span>‚Ç±{menuTotal}</span>
            </div>
            
            {spicyTotal > 0 && (
              <div className="flex justify-between text-gray-700">
                <span>Spicy Extra:</span>
                <span>‚Ç±{spicyTotal}</span>
              </div>
            )}
            
            <hr className="my-2" />
            
            <div className="flex justify-between text-xl font-bold text-red-500">
              <span>TOTAL:</span>
              <span>‚Ç±{grandTotal}</span>
            </div>
            
            {orderType === "delivery" && (
              <p className="text-sm text-gray-600 mt-2">
                * Delivery fee will be added based on distance
              </p>
            )}
          </div>
        )}

        {/* Submit Button */}
        <button
          type="submit"
          disabled={!hasItemsSelected}
          className={`w-full py-4 rounded-lg font-bold text-lg transition-all ${
            hasItemsSelected
              ? "bg-red-500 hover:bg-red-600 text-white shadow-lg hover:shadow-xl"
              : "bg-gray-300 text-gray-500 cursor-not-allowed"
          }`}
        >
          {hasItemsSelected ? 
            `üöÄ Send Order via ${messagingOption === 'whatsapp' ? 'WhatsApp' : messagingOption === 'sms' ? 'SMS' : 'Viber'}` 
            : "Select items to continue"
          }
        </button>
      </form>
    </div>
  );
};

export default OrderForm;
