"use client";

import React, { useState } from 'react';
import Menu, { MenuSelection, SpicySelection } from '@/components/Menu';
import OrderForm from '@/components/OrderForm';
import { cheeseMenu, seafoodMenu, drinksMenu } from '@/lib/menuData';

const HomePage: React.FC = () => {
  const [selections, setSelections] = useState<MenuSelection>({});
  const [spicySelections, setSpicySelections] = useState<SpicySelection>({});

  const handleQuantityChange = (itemId: string, quantity: number) => {
    setSelections((prev) => ({ ...prev, [itemId]: quantity }));
    // Clear spicy selection if quantity becomes 0
    if (quantity === 0) {
      setSpicySelections((prev) => ({ ...prev, [itemId]: false }));
    }
  };

  const handleSpicyChange = (itemId: string, isSpicy: boolean) => {
    setSpicySelections((prev) => ({ ...prev, [itemId]: isSpicy }));
  };

  // Calculate the menu total by iterating over all categories
  const calcMenuTotal = (selections: MenuSelection): number => {
    let total = 0;
    const allItems = [...cheeseMenu, ...seafoodMenu, ...drinksMenu];
    allItems.forEach((item) => {
      const qty = selections[item.id] || 0;
      total += qty * item.price;
    });
    return total;
  };

  // Calculate spicy total
  const calcSpicyTotal = (selections: MenuSelection, spicySelections: SpicySelection): number => {
    let total = 0;
    const allItems = [...cheeseMenu, ...seafoodMenu]; // Only takoyaki items can be spicy
    allItems.forEach((item) => {
      const qty = selections[item.id] || 0;
      const isSpicy = spicySelections[item.id] || false;
      if (qty > 0 && isSpicy) {
        total += qty * 2; // ‚Ç±2 per spicy item
      }
    });
    return total;
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-pink-50">
      {/* Header */}
      <header className="bg-gradient-to-r from-red-500 to-red-600 text-white shadow-lg">
        <div className="container mx-auto px-4 py-12 text-center">
          <h1 className="text-5xl md:text-6xl font-bold mb-4 tracking-tight">
            üç° Nippon - nom
          </h1>
          <p className="text-xl md:text-2xl font-light opacity-90 mb-2">
            Authentic Takoyaki Experience
          </p>
          <p className="text-lg opacity-80">
            Fresh ‚Ä¢ Delicious ‚Ä¢ Made with Love
          </p>
        </div>
      </header>

      {/* Hero Section */}
      <section className="bg-white shadow-sm">
        <div className="container mx-auto px-4 py-8">
          <div className="text-center max-w-3xl mx-auto">
            <h2 className="text-3xl font-bold text-gray-800 mb-4">
              Welcome to NIPPON - NOM! üéå
            </h2>
            <p className="text-lg text-gray-600 leading-relaxed">
              Treat yourself to our authentic Japanese takoyaki made with premium ingredients ‚Äî from cheesy classics to flavorful seafood favorites, perfectly paired with our refreshing Poppin' Soda drinks.
            </p>
          </div>
        </div>
      </section>

      {/* Main Content */}
      <main className="container mx-auto px-4 py-12">
        <div className="grid grid-cols-1 xl:grid-cols-3 gap-8">
          {/* Menu Section */}
          <div className="xl:col-span-2">
            <div className="bg-white rounded-xl shadow-lg p-6 border border-pink-100">
              <div className="text-center mb-8">
                <h2 className="text-4xl font-bold text-gray-800 mb-2">
                  üçΩÔ∏è Our Menu
                </h2>
                <p className="text-gray-600">
                  Select your favorite takoyaki and drinks
                </p>
              </div>
              <Menu 
                selections={selections} 
                spicySelections={spicySelections}
                onChange={handleQuantityChange}
                onSpicyChange={handleSpicyChange}
              />
            </div>
          </div>

          {/* Order Form Section */}
          <div className="xl:col-span-1">
            <div className="sticky top-8">
              <OrderForm 
                selections={selections} 
                spicySelections={spicySelections}
                calcMenuTotal={calcMenuTotal}
                calcSpicyTotal={calcSpicyTotal}
              />
            </div>
          </div>
        </div>
      </main>

      {/* Footer */}
      <footer className="bg-gray-800 text-white py-8 mt-16">
        <div className="container mx-auto px-4 text-center">
          <div className="mb-4">
            <h3 className="text-2xl font-bold text-red-400 mb-2">
              üç° Nippon - nom
            </h3>
            <p className="text-gray-300">
              Bringing authentic Japanese flavors to your doorstep
            </p>
          </div>
          
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-6">
            <div>
              <h4 className="font-semibold text-pink-300 mb-2">üìû Contact</h4>
              <p className="text-gray-300">09217521870</p>
            </div>
            <div>
              <h4 className="font-semibold text-pink-300 mb-2">üïí Hours</h4>
              <p className="text-gray-300">Daily: 2:00 PM - 10:00 PM</p>
            </div>
            <div>
              <h4 className="font-semibold text-pink-300 mb-2">üöö Delivery</h4>
              <p className="text-gray-300">‚Ç±20 base + ‚Ç±10/km</p>
            </div>
          </div>
          
          <div className="border-t border-gray-700 pt-4">
            <p className="text-gray-400 text-sm">
              ¬© 2020 Nippon - nom. Made with ‚ù§Ô∏è for takoyaki lovers.
            </p>
          </div>
        </div>
      </footer>
    </div>
  );
};

export default HomePage;
