"use client";

import React from 'react';
import { cheeseMenu, seafoodMenu, drinksMenu, MenuItem, DrinkItem } from '@/lib/menuData';

export interface MenuSelection {
  [itemId: string]: {
    quantity: number;
    pieceOption?: "4pcs" | "6pcs" | "10pcs";
  };
}

export interface SpicySelection {
  [itemId: string]: boolean;
}

interface MenuProps {
  selections: MenuSelection;
  spicySelections: SpicySelection;
  onChange: (itemId: string, quantity: number, pieceOption?: "4pcs" | "6pcs" | "10pcs") => void;
  onSpicyChange: (itemId: string, isSpicy: boolean) => void;
}

const renderTakoyakiSection = (
  title: string, 
  items: MenuItem[], 
  selections: MenuSelection,
  spicySelections: SpicySelection,
  onChange: MenuProps['onChange'],
  onSpicyChange: MenuProps['onSpicyChange']
) => (
  <section className="mb-8">
    <h2 className="text-2xl font-bold text-red-500 mb-4 border-b-2 border-red-200 pb-2">
      {title}
    </h2>
    <div className="grid grid-cols-1 gap-4">
      {items.map((item) => {
        const selection = selections[item.id] || { quantity: 0, pieceOption: "4pcs" };
        const currentPrice = item.prices[selection.pieceOption || "4pcs"];
        
        return (
          <div 
            key={item.id} 
            className="p-4 border rounded-lg border-pink-200 bg-white shadow-sm hover:shadow-md transition-shadow"
          >
            <div className="flex justify-between items-start mb-3">
              <div className="flex-1">
                <h3 className="text-lg font-semibold text-gray-800 mb-1">{item.name}</h3>
                {item.description && (
                  <p className="text-sm text-gray-600">{item.description}</p>
                )}
              </div>
              <div className="text-lg font-bold text-red-500 ml-4">
                ‚Ç±{currentPrice}
              </div>
            </div>
            
            <div className="space-y-3">
              {/* Piece Selection */}
              <div>
                <label className="block text-gray-700 font-medium mb-2">
                  Choose Pieces:
                </label>
                <div className="grid grid-cols-3 gap-2">
                  {(["4pcs", "6pcs", "10pcs"] as const).map((option) => (
                    <label 
                      key={option}
                      className={`p-2 border rounded cursor-pointer text-center transition-all ${
                        selection.pieceOption === option
                          ? "border-red-500 bg-red-50 text-red-700"
                          : "border-gray-300 hover:border-gray-400"
                      }`}
                    >
                      <input
                        type="radio"
                        name={`pieces-${item.id}`}
                        value={option}
                        checked={selection.pieceOption === option}
                        onChange={() => onChange(item.id, selection.quantity, option)}
                        className="sr-only"
                      />
                      <div className="text-xs font-medium">{option}</div>
                      <div className="text-xs text-gray-600">‚Ç±{item.prices[option]}</div>
                    </label>
                  ))}
                </div>
              </div>

              {/* Quantity Selection */}
              <div className="flex items-center justify-between">
                <label className="text-gray-700 font-medium" htmlFor={item.id}>
                  Quantity:
                </label>
                <div className="flex items-center space-x-2">
                  <button
                    type="button"
                    onClick={() => onChange(item.id, Math.max(0, selection.quantity - 1), selection.pieceOption)}
                    className="w-8 h-8 rounded-full bg-gray-200 hover:bg-gray-300 flex items-center justify-center text-gray-600 font-bold"
                  >
                    -
                  </button>
                  <input
                    type="number"
                    id={item.id}
                    min="0"
                    value={selection.quantity}
                    onChange={(e) => onChange(item.id, Math.max(0, parseInt(e.target.value) || 0), selection.pieceOption)}
                    className="w-16 text-center border rounded-md p-1 border-gray-300 focus:border-pink-400 focus:outline-none"
                  />
                  <button
                    type="button"
                    onClick={() => onChange(item.id, selection.quantity + 1, selection.pieceOption)}
                    className="w-8 h-8 rounded-full bg-red-500 hover:bg-red-600 flex items-center justify-center text-white font-bold"
                  >
                    +
                  </button>
                </div>
              </div>

              {/* Spicy option for each item */}
              {selection.quantity > 0 && (
                <div className="flex items-center justify-between">
                  <label className="flex items-center space-x-2 cursor-pointer">
                    <input
                      type="checkbox"
                      checked={spicySelections[item.id] || false}
                      onChange={(e) => onSpicyChange(item.id, e.target.checked)}
                      className="w-4 h-4 text-red-500 border-gray-300 rounded focus:ring-red-500"
                    />
                    <span className="text-sm text-gray-700">üå∂Ô∏è Make it spicy</span>
                  </label>
                  <span className="text-sm text-red-500 font-medium">+‚Ç±2</span>
                </div>
              )}
            </div>
          </div>
        );
      })}
    </div>
  </section>
);

const renderDrinksSection = (
  title: string, 
  items: DrinkItem[], 
  selections: MenuSelection,
  onChange: MenuProps['onChange']
) => (
  <section className="mb-8">
    <h2 className="text-2xl font-bold text-red-500 mb-4 border-b-2 border-red-200 pb-2">
      {title}
    </h2>
    <div className="grid grid-cols-1 gap-4">
      {items.map((item) => {
        const selection = selections[item.id] || { quantity: 0 };
        
        return (
          <div 
            key={item.id} 
            className="p-4 border rounded-lg border-pink-200 bg-white shadow-sm hover:shadow-md transition-shadow"
          >
            <div className="flex justify-between items-start mb-3">
              <div className="flex-1">
                <h3 className="text-lg font-semibold text-gray-800 mb-1">{item.name}</h3>
                {item.description && (
                  <p className="text-sm text-gray-600">{item.description}</p>
                )}
              </div>
              <div className="text-lg font-bold text-red-500 ml-4">
                ‚Ç±{item.price}
              </div>
            </div>
            
            <div className="flex items-center justify-between">
              <label className="text-gray-700 font-medium" htmlFor={item.id}>
                Quantity:
              </label>
              <div className="flex items-center space-x-2">
                <button
                  type="button"
                  onClick={() => onChange(item.id, Math.max(0, selection.quantity - 1))}
                  className="w-8 h-8 rounded-full bg-gray-200 hover:bg-gray-300 flex items-center justify-center text-gray-600 font-bold"
                >
                  -
                </button>
                <input
                  type="number"
                  id={item.id}
                  min="0"
                  value={selection.quantity}
                  onChange={(e) => onChange(item.id, Math.max(0, parseInt(e.target.value) || 0))}
                  className="w-16 text-center border rounded-md p-1 border-gray-300 focus:border-pink-400 focus:outline-none"
                />
                <button
                  type="button"
                  onClick={() => onChange(item.id, selection.quantity + 1)}
                  className="w-8 h-8 rounded-full bg-red-500 hover:bg-red-600 flex items-center justify-center text-white font-bold"
                >
                  +
                </button>
              </div>
            </div>
          </div>
        );
      })}
    </div>
  </section>
);

const Menu: React.FC<MenuProps> = ({ selections, spicySelections, onChange, onSpicyChange }) => {
  return (
    <div className="space-y-8">
      {renderTakoyakiSection("Cheese Category", cheeseMenu, selections, spicySelections, onChange, onSpicyChange)}
      {renderTakoyakiSection("Seafood Category", seafoodMenu, selections, spicySelections, onChange, onSpicyChange)}
      {renderDrinksSection("Poppin' Soda", drinksMenu, selections, onChange)}
    </div>
  );
};

export default Menu;
